// components/RoomView.js
import { useEffect, useState } from "react";
import { ref, set, onValue, update, onDisconnect, remove, get } from "firebase/database";
import { getDB, getFirebaseApp } from "../lib/firebaseClient";
import { getAuth } from "firebase/auth";

/**
 * RoomView ajustado:
 * - remove automaticamente o participante ao desmontar o componente
 *   (quando o usu√°rio clicar no menu Home).
 */
export default function RoomView({ roomId, user: userProp }) {
  const [part, setPart] = useState({});
  const [selected, setSelected] = useState(null);
  const [roomName, setRoomName] = useState("");

  const OPT = [3, 6, 9, 12, 15, 18, 21, 24, 27, 30, 33, 36];

  useEffect(() => {
    const auth = getAuth(getFirebaseApp());
    const user = userProp || auth.currentUser;
    if (!user || !roomId) return;

    const uid = user.uid;
    const displayName = user.displayName || "Usu√°rio";

    const db = getDB();

    const salaRef = ref(db, `salas/${roomId}/nome`);
    const unsubSala = onValue(salaRef, (snap) => {
      setRoomName(snap.val() || "Sala sem nome");
    });

    const participantsRefRoot = ref(db, `salas/${roomId}/participantes`);

    get(participantsRefRoot).then((snap) => {
      const val = snap.val() || {};
      const hadUid = !!val[uid];
      const hadDisplayNameKey = !!val[displayName];

      if (!hadUid && hadDisplayNameKey) {
        const old = val[displayName];
        const newData = {
          nome: displayName,
          escolha: old?.escolha ?? null,
          revelado: old?.revelado ?? false,
          migratedFrom: displayName,
          migratedAt: Date.now()
        };
        set(ref(db, `salas/${roomId}/participantes/${uid}`), newData)
          .then(() => remove(ref(db, `salas/${roomId}/participantes/${displayName}`)))
          .catch((err) => console.error("Erro migra√ß√£o participante:", err));
      }
    });

    const userRef = ref(db, `salas/${roomId}/participantes/${uid}`);
    set(userRef, {
      nome: displayName,
      escolha: null,
      revelado: false,
      joinedAt: Date.now()
    }).catch((err) => console.error("Erro ao set participante:", err));

    onDisconnect(userRef).remove();

    const unsub = onValue(participantsRefRoot, (snap) => {
      const val = snap.val() || {};
      setPart(val);

      if (val[uid] && typeof val[uid].escolha === "number") {
        setSelected(val[uid].escolha);
      } else {
        setSelected(null);
      }
    });

    // ‚õî **AQUI √â O AJUSTE**
    // Quando clicar no Home ‚Üí este componente desmonta ‚Üí usu√°rio removido.
    return () => {
      try {
        unsub();
        unsubSala();
      } catch {}

      // üî• remove o usu√°rio da sala ao sair da p√°gina
      remove(userRef).catch(() => {});
    };
  }, [roomId, userProp]);

  function getCurrentUser() {
    const auth = getAuth(getFirebaseApp());
    return auth.currentUser;
  }

  function choose(v) {
    const user = getCurrentUser();
    if (!user) return alert("Usu√°rio n√£o autenticado.");

    const uid = user.uid;
    const db = getDB();
    setSelected(v);

    update(ref(db, `salas/${roomId}/participantes/${uid}`), {
      escolha: v,
      revelado: false
    });
  }

  function revealAll() {
    const db = getDB();
    Object.keys(part).forEach((p) =>
      update(ref(db, `salas/${roomId}/participantes/${p}`), { revelado: true })
    );
  }

  function hideAll() {
    const db = getDB();
    Object.keys(part).forEach((p) =>
      update(ref(db, `salas/${roomId}/participantes/${p}`), { revelado: false })
    );
  }

  function clearAll() {
    const db = getDB();
    Object.keys(part).forEach((p) =>
      update(ref(db, `salas/${roomId}/participantes/${p}`), { escolha: null, revelado: false })
    );
    setSelected(null);
  }

  function participantDisplayName(key, data) {
    if (data?.nome) return data.nome;
    return key;
  }

  return (
    <div className="card">
      <h2>
        Sala: <strong>{roomName}</strong>
      </h2>

      <p>
        Voc√™:{" "}
        <strong>
          {participantDisplayName(getCurrentUser()?.uid, {
            nome: getCurrentUser()?.displayName
          })}
        </strong>
      </p>

      <div className="grid" style={{ display: "grid", gridTemplateColumns: "repeat(4, 1fr)", gap: 8 }}>
        {OPT.map((o) => (
          <button
            key={o}
            onClick={() => choose(o)}
            style={{
              padding: "12px",
              borderRadius: "8px",
              border: selected === o ? "2px solid #4A90E2" : "1px solid #ccc",
              backgroundColor: selected === o ? "#4A90E2" : "#fff",
              color: selected === o ? "#fff" : "#333",
              fontWeight: selected === o ? "bold" : "normal"
            }}
          >
            {o}h
          </button>
        ))}
      </div>

      <div style={{ marginTop: 12, display: "flex", gap: 12 }}>
        <button className="reveal-btn" onClick={revealAll}>
          Mostrar escolhas
        </button>

        <button
          onClick={hideAll}
          style={{
            backgroundColor: "#0a74da",
            color: "white",
            padding: "10px 16px",
            borderRadius: "8px"
          }}
        >
          Ocultar escolhas
        </button>

        <button className="clear-btn" onClick={clearAll}>
          Limpar
        </button>
      </div>

      <h3 style={{ marginTop: 16 }}>Resultados</h3>

      <div>
        {Object.entries(part).map(([key, data]) => {
          let show;

          if (!data?.escolha && data?.escolha !== 0) {
            show = <span style={{ opacity: 0.6 }}>‚è≥ aguardando...</span>;
          } else if (data.revelado) {
            show = `${data.escolha}h`;
          } else {
            show = (
              <span style={{ fontWeight: "bold" }}>
                üëÅÔ∏è revelar
              </span>
            );
          }

          return (
            <div
              key={key}
              style={{
                padding: "6px 0",
                borderBottom: "1px solid #eee",
                display: "flex",
                justifyContent: "space-between"
              }}
            >
              <div>
                {participantDisplayName(key, data)}: <strong>{show}</strong>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}